let count = 0;
let timer = 0;
let loadArray = [];

for (let i = 0; i < 50; i++) loadArray[i] = '-';
const h1El = document.querySelector('#message');
const h1El2 = document.querySelector('#time');
const h2El = document.querySelector('#ment');

let loadTextNode = document.createTextNode(loadArray.join(''));
let endTextNode = document.createTextNode(' status: connected successful');
let timerTextNode = document.createTextNode(`Time: ${timer.toFixed(2)}ms`); // ÌÉÄÏù¥Î®∏ Ï¥àÍ∏∞Í∞í ÏÑ§Ï†ï

// h1ElÍ≥º h1El2Ïóê ÌÖçÏä§Ìä∏ ÎÖ∏Îìú Ï∂îÍ∞Ä
h1El.appendChild(loadTextNode);
h1El2.appendChild(timerTextNode);

const onlineStatus = document.createElement('span'); // ÏÉÅÌÉú ÌëúÏãúÎ•º ÏúÑÌïú span ÏöîÏÜå ÏÉùÏÑ±
onlineStatus.textContent = '    ‚óè'; // ÎèôÍ∑∏ÎùºÎØ∏ Ï†ê Ï∂îÍ∞Ä
onlineStatus.style.color = 'green'; // Ï¥àÎ°ùÏÉâÏúºÎ°ú ÏÑ§Ï†ï
onlineStatus.style.fontSize = '10px'; // Ï†êÏùò ÌÅ¨Í∏∞ Ï°∞Ï†à
onlineStatus.style.marginRight = '5px'; // ÌÖçÏä§Ìä∏ÏôÄÏùò Í∞ÑÍ≤© Ï°∞Ï†à


const loadInterval = setInterval(() => {
  loadArray[count] = '*';
  loadTextNode.data = loadArray.join('');
  timer += 0.0001;
  timerTextNode.data = `Time: ${timer.toFixed(4)}ms`; // ÌÉÄÏù¥Î®∏ ÏóÖÎç∞Ïù¥Ìä∏
  count++;
  if (count === loadArray.length) {
    clearInterval(loadInterval);
    const br1 = document.createElement('br'); // <br> ÏöîÏÜå ÏÉùÏÑ±
    const br2 = document.createElement('br'); // <br> ÏöîÏÜå ÏÉùÏÑ±
    const br3 = document.createElement('br'); // <br> ÏöîÏÜå ÏÉùÏÑ±
    h2El.appendChild(endTextNode); // Ïó∞Í≤∞ ÏÑ±Í≥µ Î©îÏãúÏßÄ Ï∂îÍ∞Ä
    h2El.appendChild(onlineStatus);
    h2El.appendChild(br1); // <br> ÏöîÏÜå Ï∂îÍ∞ÄÌïòÏó¨ Ï§ÑÎ∞îÍøà
    h2El.appendChild(document.createTextNode(`Time: ${timer.toFixed(3)}ms`)); // ÌÉÄÏù¥Î®∏ ÏµúÏ¢Ö Í∞í Ï∂îÍ∞Ä
    h2El.appendChild(br2);
    // 'IP Address:' Î©îÏãúÏßÄÏôÄ 'hidden' ÏÉâÏÉÅ Î≥ÄÍ≤Ω
    const ipAddressText = document.createTextNode('IP Address: ');
    const hiddenTextSpan = document.createElement('span'); // 'hidden'ÏùÑ ÏúÑÌïú span ÏöîÏÜå
    hiddenTextSpan.textContent = 'hidden';
    hiddenTextSpan.style.color = 'gray'; // Í∏ÄÏûê ÏÉâÏÉÅÏùÑ Î≥ÄÍ≤ΩÌï† ÏÉâÏÉÅ ÏßÄÏ†ï
    // 'IP Address:'ÏôÄ 'hidden' Î©îÏãúÏßÄ Ï∂îÍ∞Ä
    h2El.appendChild(ipAddressText);
    h2El.appendChild(hiddenTextSpan);
    h2El.appendChild(br3);
    const policyText = document.createTextNode('Policy: ');
    const openTextSpan = document.createElement('span');
    openTextSpan.textContent = 'All-Standards';
    openTextSpan.style.color = 'brown';
    h2El.appendChild(policyText);
    h2El.appendChild(openTextSpan);
  }
}, 15);
